import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    Text,
    TouchableOpacity,
    Dimensions
} from 'react-native'
import {THEME} from "../../../../theme";
import {ButtonInCart} from "../../ButtonInCart";
import {Price} from "./Price";
import {ModyficateBlock} from "../Modyficate/ModyficateBlock";
import {NumberFormatPrice} from "../../../../templateComponents/NumberFormat/NumberFormat";
import {ButtonOpenProductModify} from "../../ButtonOpenProductModify";
import {getTypeDevice} from "../../../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";
import {useSelector} from "react-redux";
import {ButtonEbee} from "../../../../templateComponents/Button/ButtonEbee";

export const OneProduct = ({ product, onOpen, onOpenProduct }) => {

    const idUserVK = useSelector(state => state.post.idUserVK);
    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);
    const DisplayAll = useSelector(state => state.post.displayAll);
    const DisplayAllAffiate = useSelector(state => state.post.displayAllAffiate);
    let ShowInBasket = true;

    // console.log('Данные idUserVk = ',idUserVK);
    let TextButtonInCart = 'В корзину';
    let ButtonModify = false;
    if(product?.typeProductModify === true){
        TextButtonInCart = 'Выбрать';
        ButtonModify = true;
        if(product.buttonModifyOpenModal !== ''){
            TextButtonInCart = product.buttonModifyOpenModal;
        }
    }

    let linkURL = 'https://vk.com/app'+idUserVK.appId;
    if(idUserVK?.groupId !== null){
        linkURL += '_-'+idUserVK.groupId;
    }

    // if(TypeDeliverySystem === true) {
    //     //условие доставка
    //     DisplayAll === true ?
    //         ShowInBasket = false : ShowInBasket = true
    // }else {
    //     //Условие самовывоз
    //     DisplayAllAffiate === true ?
    //         ShowInBasket = false : ShowInBasket = true
    // }

    // console.log(ShowInBasket);


    return (
        <TouchableOpacity
            // style={Dimensions.get('window').width <= 420 ? {width:"100%"}:{width:"50%"} }
            style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? {width:"48%",marginHorizontal:"1%"} : {width:"100%"}}
            activeOpacity={0.7} onPress={() => {onOpen(product);onOpenProduct(product)}}>
            <View style={styles.productBlock}>
                    <View style={ styles.textWrap }>
                        <View style={styles.TextImageProductL}>
                            <a style={{textDecoration:"none",color:"inherit"}} onClick={(e)=>{e.preventDefault();}} href={linkURL+"#getPageProduct&idCategory="+product.id_Category+"&idProduct="+product.id}>

                            <Image style={styles.image} source={{ uri: product.img }}
                                />
                            </a>
                        </View>
                        <View style={styles.TextBlockProduct}>
                            <View style={styles.BlockTitle}>
                                <Text style={styles.title}>
                                    {product.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                </Text>
                                <Text style={styles.weight}>{NumberFormatPrice(product.weight,0,'.',' ')} {product.weight_type}</Text>
                            </View>
                            {product.description !== '' ? <View style={styles.ViewDescription}>
                                <Text style={styles.description}>
                                    {product.description.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                </Text>
                            </View> : <View style={styles.ViewDescription}><Text></Text></View>}
                            {product.type_info !== '' ? <View style={styles.ViewTypeInfo}>
                                <Text style={styles.TypeInfo}>
                                    {product.type_info.trim()}
                                </Text>
                            </View> : false}


                                    <View style={styles.ViewPrice}>
                                        <Price ProductData={product}/>
                                        {
                                            ShowInBasket === true ?
                                                ButtonModify ?
                                                    <ButtonOpenProductModify Product={product} titleButton={TextButtonInCart}
                                                                             onOpen={onOpen} onOpenProduct={onOpenProduct}/>
                                                    :
                                                    <ButtonInCart Product={product}/>

                                                : <ButtonEbee
                                                    // onPress={()=>{
                                                    //     console.log('Нажали на в корзину - перекинуть на адрес');
                                                    //     navigation.navigate('AdressList', {
                                                    //         productId: 1,
                                                    //         title: "Заголовок страницы",
                                                    //         booked: false,
                                                    //         productData:[{id:1,title:"Заголовок страницы",booked:false}]
                                                    //     });
                                                    // }}
                                                    size="l" borderRadius={10} shadow={false} style={{minWidth:90,paddingTop:6,paddingBottom:7,paddingHorizontal:2,backgroundColor:"#dddddd"}}>Нет адреса</ButtonEbee>
                                        }
                                    </View>
                        </View>
                        <a style={{textDecoration:"none",color:"inherit"}} onClick={(e)=>{e.preventDefault();}} href={linkURL+"#getPageProduct&idCategory="+product.id_Category+"&idProduct="+product.id}>

                        <ModyficateBlock ModyficateProduct={product.modyficate} idProduct={product.id}/>
                        </a>
                    </View>
            </View>
        </TouchableOpacity>
    )
};


export class OneProductPure extends React.PureComponent{

    render(){

        const product = this.props.product;
        const onOpenProduct = this.props.onOpenProduct;
        const onOpen = this.props.onOpen;

        return(
            <TouchableOpacity activeOpacity={0.7} onPress={() => {onOpen(product);onOpenProduct(product)}}>
                <View style={styles.productBlock}>
                    <View style={styles.textWrap}>
                        <Image style={styles.image} source={{ uri: product.img }} />
                        <View style={styles.TextBlockProduct}>
                            <View style={styles.BlockTitle}>
                                <Text style={styles.title}>
                                    {product.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                </Text>
                                <Text style={styles.weight}>{NumberFormatPrice(product.weight,0,'.',' ')} {product.weight_type}</Text>
                            </View>
                            {product.description !== '' ? <View style={styles.ViewDescription}>
                                <Text style={styles.description}>
                                    {product.description.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                </Text>
                            </View> : <Text></Text>}
                            {product.type_info !== '' ? <View style={styles.ViewTypeInfo}>
                                <Text style={styles.TypeInfo}>
                                    {product.type_info.trim()}
                                </Text>
                            </View> : false}
                            <View style={styles.ViewPrice}>
                                <Price ProductData={product}/>
                                <ButtonInCart Product={product}/>
                            </View>
                        </View>
                        <ModyficateBlock ModyficateProduct={product.modyficate} idProduct={product.id}/>
                    </View>
                </View>
            </TouchableOpacity>
        )
    }
}

const styles = StyleSheet.create({
    productBlock: {
        marginBottom: 0,
        overflow: 'hidden',
        boxSizing:"border-box",
        // padding:15
    },
    image: {
        width: Dimensions.get('window').width > 370 ? THEME.WIDTH_PRODUCT_ONE_PRODUCT_IMG : Dimensions.get('window').width<346 ? THEME.WIDTH_PRODUCT_ONE_PRODUCT_IMG-45 : THEME.WIDTH_PRODUCT_ONE_PRODUCT_IMG-32,
        height: Dimensions.get('window').width > 370 ? THEME.HEIGHT_PRODUCT_ONE_PRODUCT_IMG : Dimensions.get('window').width<346 ? THEME.HEIGHT_PRODUCT_ONE_PRODUCT_IMG-45 : THEME.HEIGHT_PRODUCT_ONE_PRODUCT_IMG-32,
        marginRight:5,
        borderRadius:THEME.BORDER_RADIUS_DEFAULT,
    },
    TextImageProductL:{
        width: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? "35%" : Dimensions.get('window').width >460 ? "31%" :  Dimensions.get('window').width<346 ? "35%" : "43%",
        // paddingRight:"1%"
    },
    TextBlockProduct:{
        width:  getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? "65%" : Dimensions.get('window').width >460 ? "69%" :   Dimensions.get('window').width<346 ? "65%" :  "57%",
        paddingRight:"1%"
    },
    BlockTitle:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"stretch",
        width:"99%"
    },
    textWrap: {
        flexDirection:"row",
        justifyContent:"space-between",
        backgroundColor: '#ffffff',
        paddingVertical: 5,
        alignItems: 'center',
        width: '100%'
    },
    title: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 14 : Dimensions.get('window').width >460 ? 14 : 13,
        width:"70%",
    },
    weight:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    description: {
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontFamily: 'open-regular',
        fontSize:getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 12 : Dimensions.get('window').width >460 ? 12 : 9.5
    },
    ViewDescription:{
        width:'97%',
        paddingBottom:10
    },
    ViewPrice:{
        width:'100%',
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    TypeInfo: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-regular',
        fontSize:10,
        backgroundColor: THEME.TABS_BACKGROUND_COLOR,
        borderWidth:1,
        borderColor:THEME.MAIN_COLOR,
        paddingVertical:3,
        paddingBottom:5,
        paddingHorizontal:7,
        borderRadius:10
    },
    ViewTypeInfo:{
        width:'80%',
        flexDirection:"row",
        alignItems:"center",
        justifyContent: "flex-start",
        paddingBottom: 5
    }
})
