import {NumberFormatPrice} from "../../templateComponents/NumberFormat/NumberFormat";

export const getTypeDiscountPromoInfo = (sale_type,sale,type_priceOld)=>{
    let sales = 0;

    let proc_Sum = 0;

    switch (sale_type) {
        case "0":
            //скидка в %.
            sales = sale / 100;
            proc_Sum = NumberFormatPrice(type_priceOld * sales,0,'','');
            break;
        case "1":
            //скидка в рулях.
            sales = sale;
            proc_Sum = sales;
            if (proc_Sum < 0) {
                proc_Sum = 0;
            }
            break;
        default:
            console.log('ХЗ какая скидка в % или рублях');
            //хз какая скидка  -считатем что скидка в рулях.
            sales = sale;
            proc_Sum = sales;
            if (proc_Sum < 0) {
                proc_Sum = 0;
            }
    }
    
    return proc_Sum;
};