export const GetPriceProductBasket = ({ ProductData, countProduct, modifyLst = [] }) => {

    let sumModifyPrice = 0;

    modifyLst.forEach((d,n)=>{
        sumModifyPrice += d.price*1;
    });

    //console.log('Для вывода инфы -------=============ЮЮЮЮЮЮЮЮЮ',ProductData);
    if(ProductData.price_discount !== false){
        return NumberFormatPrice(((ProductData.price_discount.new_price*1)+(sumModifyPrice*1))*countProduct,0,'.',' ');
    }else{
        return NumberFormatPrice(((ProductData.price*1)+(sumModifyPrice*1))*countProduct,0,'.',' ');
    }
};


export const GetPriceProductBasketDiscount = ( ProductData, countProduct, modifyLst = [], BDayStatus=false, BDayDiscountBasket=false, BDayActionAdition=false) => {
    let sumModifyPrice = 0;

    modifyLst.forEach((d,n)=>{
        sumModifyPrice += d.price*1;
    });


    //console.log('Для вывода инфы -------=============ЮЮЮЮЮЮЮЮЮ',ProductData);
    if(ProductData.price_discount !== false){

        let Sum =0;
        let BDayTotal=0
        // console.log("WTF3",ProductData)
        Sum = (((ProductData.price_discount.old_price*1)+(sumModifyPrice*1))*countProduct)-(((ProductData.price_discount.new_price*1)+(sumModifyPrice*1))*countProduct);
        // console.log("WTF1",Sum)
        if(BDayStatus.BDay === true) {
            // Развилка на будущее если обрастет условиями.... тут творится хрень в хоторой я запутолся XD и сюда надо много чего еще....
            // console.log('BDayActionAdition',BDayActionAdition)
            if(BDayActionAdition === true){
                BDayTotal=((((ProductData.price_discount.new_price*1)+(sumModifyPrice*1))*countProduct)-((((ProductData.price_discount.new_price*1)+(sumModifyPrice*1))*countProduct)*BDayDiscountBasket))
                BDayTotal=(((ProductData.price_discount.new_price*1)+(sumModifyPrice*1))*countProduct)-BDayTotal
                Sum = BDayTotal
            }else {
                Sum=0
            }

        } // переработать
        if(Sum<=0){Sum = 0;}
        // console.log("WTF0",Sum,BDayTotal)
        return Sum;
    }else{
        if(BDayStatus.BDay === true) {
            // console.log("ЭХЕХЕХЕЙ",ProductData)
            let Sum =0;
            Sum =   (((ProductData.price*1)+(sumModifyPrice*1))*countProduct)
            Sum = Sum*BDayDiscountBasket
            // console.log("Быть или не быть")
            if(Sum<=0){Sum = 0;}
            return Sum;
        }
        return 0;
    }
};


export const GetPriceProductBasketVisual = ({ ProductData, countProduct, stylePrice,styleNewPrice,styleOldPrice , fontSize = 14, fontFamily = 'bold', priceBlock = "row", modifyList = [], countProductFree = 0, BDayBasket=false, BDayDiscountBasket=null }) => {

    let sumModifyPrice = 0;
    let FreeInfo = false;
    let countProductBasket = countProduct;

    // console.log('Передаем это');

    modifyList.forEach((d,n)=>{
            sumModifyPrice += d.price*1;
    });
    // console.log("modidyList",modifyList,sumModifyPrice)


    if(countProductFree > 0){
        if(countProduct>0) {
            // console.log('Что делаем тут - = - = -= - countProductBasket - countProductFree',countProductBasket, countProductFree );
            countProductBasket = countProduct - countProductFree;
        }

        FreeInfo = <>
            <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                {NumberFormatPrice(((((ProductData.price*1)+(sumModifyPrice*1))*countProductBasket)-((((ProductData.price*1)*BDayDiscountBasket)+((sumModifyPrice*1)*BDayDiscountBasket))*countProductBasket)),0,'.',' ')} ₽
            </Text>
            <Text style={[styles.Oldprice,styleOldPrice]}>
                {NumberFormatPrice(((ProductData.price*1)+(sumModifyPrice*1))*countProduct,0,'.',' ')} ₽
            </Text>
        </>;

    }else{
        // FreeInfo = <Text style={{...styles.Oldprice,...styleOldPrice}}>
        //     Тут что то есть
        // </Text>
    }


    // console.log('Для вывода инфы -------=============ХХХХХХХХХ',ProductData);
    if(ProductData.price_discount !== false){
        if(BDayBasket.BDay === true){

            return (
                <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                    <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                        {((ProductData.price_discount.new_price*1)+(sumModifyPrice*1))*countProductBasket} ₽
                    </Text>
                    <Text style={[styles.Oldprice,styleOldPrice]}>
                        {NumberFormatPrice((((ProductData.price_discount.old_price*1)-(ProductData.price_discount.old_price*1)*BDayDiscountBasket)+((sumModifyPrice*1)-(sumModifyPrice*1)*BDayDiscountBasket))*countProductBasket,0,'.',' ')} ₽
                    </Text>
                    {/*{FreeInfo}*/}
                </View>
            )
        }
        return (
            <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                    {((ProductData.price_discount.new_price*1)+(sumModifyPrice*1))*countProductBasket} ₽
                </Text>
                <Text style={[styles.Oldprice,styleOldPrice]}>
                    {NumberFormatPrice(((ProductData.price_discount.old_price*1)+(sumModifyPrice*1))*countProductBasket,0,'.',' ')} ₽
                </Text>
                {/*{FreeInfo}*/}
            </View>
        )
    }else{
        // console.log("Сегодня ДР,",BDayBasket,"Сама скидка",BDayDiscountBasket,"СКОЛЬКО",ProductData.price)
        if(BDayBasket.BDay === true){
            // {/*<Text style={[[styles.price,stylePrice],{fontSize:fontSize},{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>*/}
            // {/*    {NumberFormatPrice((((ProductData.price*1)-(ProductData.price*1)*BDayDiscountBasket))+(sumModifyPrice*1)*countProductBasket,0,'.',' ')} ₽*/}
            // {/*</Text>*/}
            // модификаторы под скидкой или нет?
            return (
                <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                    {FreeInfo !== false ?
                        FreeInfo :
                        <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>

                            <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                                {NumberFormatPrice(((((ProductData.price*1)-(ProductData.price*1)*BDayDiscountBasket))+((sumModifyPrice*1)-(sumModifyPrice*1)*BDayDiscountBasket))*countProductBasket,0,'.',' ')} ₽
                            </Text>
                            <Text style={[styles.Oldprice,styleOldPrice]}>
                                {NumberFormatPrice(((ProductData.price*1)+(sumModifyPrice*1))*countProductBasket,0,'.',' ')} ₽
                            </Text>

                        </View>
                    }
                </View>
            )
        }
        return (
            <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                {FreeInfo !== false ?
                FreeInfo :
                    <Text style={[[styles.price,stylePrice],{fontSize:fontSize},{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                        {NumberFormatPrice(((ProductData.price*1)+(sumModifyPrice*1))*countProductBasket,0,'.',' ')} ₽
                    </Text>
                }
            </View>
        )
    }
};

export const GetPriceProductBasketPromokodeVisual = ({
                                                         BasketPromoDiscount,
                                                         ProductId,
                                                         countProduct = 0,
                                                         typeProduct,
                                                         stylePrice,
                                                         styleNewPrice,
                                                         styleOldPrice ,
                                                         fontSize = 14,
                                                         fontFamily = 'bold',
                                                         priceBlock = "row",
                                                         modifyList = [],
                                                         ProductData = {},
                                                         countProductFree = 0,
                                                         numberProductInCart = 0,
                                                         numberTypeProductInCart = 0,
                                                         BDayBasket=false,
                                                         BDayDiscountBasket=null,
                                                         BDayActionAdition=null
}) => {

    let SuccesProduct = false;
    let NumberArrayKeyProduct = 0;
    let FreeInfo = false;
    let FreeInfoNoDiscount = false;

    let sumModifyPrice = 0;
    let countProductBasket = countProduct;

    // modifyList.forEach((d,n)=>{
    //     sumModifyPrice += d.price*1;
    // });

    //console.log('Сумма модификаторов для товара равна = ',sumModifyPrice);

    // console.log('Для вывода инфы -------=============Ю-Ю-ЮЮЮЮЮЮЮ',BasketPromoDiscount);



    BasketPromoDiscount.forEach((item,number)=>{
        //console.log(item,number);
        if(item.id_product === ProductId && item.type_product === typeProduct && CheckModifyProductAccordance(item.modifyListProduct,modifyList)){
            if(item.sumPriceSale !== item.sumPriceDef || item.sumPriceDef == '0'){
                SuccesProduct = true;
                NumberArrayKeyProduct = number;
            }else{
                // SuccesProduct = false;
                NumberArrayKeyProduct = number;
                // console.log('Суммы совпали, значит скидки нет ',item.sumPriceSale,item.sumPriceDef,item.sumDiscount);
            }


            if(ProductData.price_discount !== false){
                if(ProductData.price_discount.new_price == '0'){
                    BasketPromoDiscount[NumberArrayKeyProduct].sumPriceSale = 0;
                    BasketPromoDiscount[NumberArrayKeyProduct].sumDiscount = item.sumPriceDef;
                }
            }

            //console.log(NumberArrayKeyProduct);
        }
    });

   // console.log(BasketPromoDiscount,NumberArrayKeyProduct);


    if(countProductFree > 0){
        if(countProduct>0) {
            //console.log('Что делаем тут - = - = -= - countProductBasket - countProductFree',countProductBasket, countProductFree );
            countProductBasket = countProduct*1 - countProductFree*1;
            //console.log('После преобразования, цену для товара нужно ставить в кол-ве ',countProductBasket);
        }

        if(ProductData.price_discount !==false) {

            if(BDayBasket.BDay === true){
                //Если есть товары по промокоду и товары по скидкам помимо него, плюс товары по 3=2 то нужно все пересчитывать, чтобы выдавать верную сумму в поле + BDay
                if(BasketPromoDiscount[NumberArrayKeyProduct].tmpData.ProductListPromokodeDiscount.countProductDiscountBasket >0){
                    let neCountMinusPromoCount = countProductBasket - BasketPromoDiscount[NumberArrayKeyProduct].tmpData.ProductListPromokodeDiscount.countProductDiscountBasket;
                    if(neCountMinusPromoCount >0){
                        FreeInfo = <>
                            <Text style={[styles.Newprice, styleNewPrice, {fontFamily: "open-" + fontFamily}]}>
                                {(((ProductData.price_discount.new_price * 1) + (sumModifyPrice * 1)) * neCountMinusPromoCount)+((BasketPromoDiscount[NumberArrayKeyProduct].tmpData.ProductListPromokodeDiscount.countProductDiscountBasket*BasketPromoDiscount[NumberArrayKeyProduct].tmpData.ProductListPromokodeDiscount.priceProductDiscount)*BDayDiscountBasket)} ₽
                            </Text>
                            <Text style={[styles.Oldprice, styleOldPrice]}>
                                {NumberFormatPrice(((ProductData.price_discount.old_price * 1) + (sumModifyPrice * 1)) * countProduct, 0, '.', ' ')} ₽
                            </Text>
                        </>;
                    }

            }else {

            //Если есть товары по промокоду и товары по скидкам помимо него, плюс товары по 3=2 то нужно все пересчитывать, чтобы выдавать верную сумму в поле
            if(BasketPromoDiscount[NumberArrayKeyProduct].tmpData.ProductListPromokodeDiscount.countProductDiscountBasket >0){
                let neCountMinusPromoCount = countProductBasket - BasketPromoDiscount[NumberArrayKeyProduct].tmpData.ProductListPromokodeDiscount.countProductDiscountBasket;
                if(neCountMinusPromoCount >0){
                    FreeInfo = <>
                        <Text style={[styles.Newprice, styleNewPrice, {fontFamily: "open-" + fontFamily}]}>
                            {(((ProductData.price_discount.new_price * 1) + (sumModifyPrice * 1)) * neCountMinusPromoCount)+(BasketPromoDiscount[NumberArrayKeyProduct].tmpData.ProductListPromokodeDiscount.countProductDiscountBasket*BasketPromoDiscount[NumberArrayKeyProduct].tmpData.ProductListPromokodeDiscount.priceProductDiscount)} ₽
                        </Text>
                        <Text style={[styles.Oldprice, styleOldPrice]}>
                            {NumberFormatPrice(((ProductData.price_discount.old_price * 1) + (sumModifyPrice * 1)) * countProduct, 0, '.', ' ')} ₽
                        </Text>
                    </>;
                }
            }}
            }else{
                FreeInfo = <>
                    <Text style={[styles.Newprice, styleNewPrice, {fontFamily: "open-" + fontFamily}]}>
                        {((ProductData.price_discount.new_price * 1) + (sumModifyPrice * 1)) * countProductBasket} ₽
                    </Text>
                    <Text style={[styles.Oldprice, styleOldPrice]}>
                        {NumberFormatPrice(((ProductData.price_discount.old_price * 1) + (sumModifyPrice * 1)) * countProduct, 0, '.', ' ')} ₽
                    </Text>
                </>;
            }


        }else {
            FreeInfoNoDiscount = <>
                <Text style={[styles.Newprice, styleNewPrice, {fontFamily: "open-" + fontFamily}]}>
                    {NumberFormatPrice(((ProductData.price * 1) + (sumModifyPrice * 1)) * countProductBasket, 0, '.', ' ')} ₽
                </Text>
                <Text style={[styles.Oldprice, styleOldPrice]}>
                    {NumberFormatPrice(((ProductData.price * 1) + (sumModifyPrice * 1)) * countProduct, 0, '.', ' ')} ₽
                </Text>
            </>;
        }
    }

    if(SuccesProduct !== false){
        return (
            <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>

                {FreeInfo !== false ?
                    FreeInfo :
                    BDayBasket.BDay ?
                        BDayActionAdition?

                                <>
                                    <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                                        {NumberFormatPrice(((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceSale*1)-(BasketPromoDiscount[NumberArrayKeyProduct].sumPriceSale*1)*BDayDiscountBasket)+((sumModifyPrice*1)-((sumModifyPrice*1)*BDayDiscountBasket)),0,'.',' ')} ₽
                                    </Text>
                                    <Text style={[styles.Oldprice,styleOldPrice]}>
                                        {NumberFormatPrice((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef*1)+(sumModifyPrice*1),0,'.',' ')} ₽
                                    </Text>
                                </>
                                    :
                                <>
                                    <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                                        {NumberFormatPrice((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceSale*1)+(sumModifyPrice*1),0,'.',' ')} ₽
                                    </Text>
                                    <Text style={[styles.Oldprice,styleOldPrice]}>
                                        {NumberFormatPrice((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef*1)+(sumModifyPrice*1),0,'.',' ')} ₽
                                    </Text>
                                </>
                        :
                        <>
                            <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                                {NumberFormatPrice((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceSale*1)+(sumModifyPrice*1),0,'.',' ')} ₽
                            </Text>
                            <Text style={[styles.Oldprice,styleOldPrice]}>
                                {NumberFormatPrice((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef*1)+(sumModifyPrice*1),0,'.',' ')} ₽
                            </Text>
                        </>
                    }
            </View>
        )
    }else{
        if(BDayBasket.BDay === true){
            return (
                <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                    {FreeInfoNoDiscount !== false ?
                        FreeInfoNoDiscount :
                        <>
                        <Text
                            style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                                {NumberFormatPrice(((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef * 1)-(BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef * 1)*BDayDiscountBasket) + ((sumModifyPrice * 1)-((sumModifyPrice * 1)*BDayDiscountBasket)), 0, '.', ' ')} ₽
                        </Text>
                        <Text
                            style={[styles.Oldprice,styleOldPrice]}>
                            {NumberFormatPrice((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef * 1) + (sumModifyPrice * 1), 0, '.', ' ')} ₽
                        </Text>
                        </>
                    }
                </View>

            )
        }
        return (
            <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                {FreeInfoNoDiscount !== false ?
                    FreeInfoNoDiscount :
                    <><Text
                        style={[[styles.price, stylePrice], {fontSize: fontSize}, {fontFamily: "open-" + fontFamily}]}>
                        {NumberFormatPrice((BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef * 1) + (sumModifyPrice * 1), 0, '.', ' ')} ₽
                    </Text>
                    </>
                }
            </View>

        )
    }
};


export const GetPriceProductBasketNumber = (ProductData, countProduct, returnBoleanDiscount = false, modifyList = [], returnOldPrice = false) => {

    let SumReturn = 0;
    let Bolean = false;
    let sumModifyPrice = 0;

    modifyList.forEach((d,n)=>{
        sumModifyPrice += d.price*1;
    });

    if(ProductData.price_discount !== false){
        if(returnOldPrice === true){
            SumReturn = (((ProductData.price_discount.old_price * 1) + (sumModifyPrice * 1)) * countProduct);
        }else {
            SumReturn = (((ProductData.price_discount.new_price * 1) + (sumModifyPrice * 1)) * countProduct);
        }
        Bolean = true;
    }else{
        SumReturn = (((ProductData.price*1)+(sumModifyPrice*1))*countProduct);
        Bolean = false;
    }

    if(returnBoleanDiscount ===true){
        return Bolean;
    }else{
        return SumReturn;
    }

};



export const GetPriceProductPreorderTopping = (ProductData, countProduct, returnBoleanDiscount = false, modifyList = []) => {
    let sumModifyPrice = 0;
    let SumReturn = 0;
    let Bolean = false;

    modifyList.forEach((d,n)=>{
        sumModifyPrice += d.price*1;
    });

    if(ProductData.price_discount !== false){
        SumReturn = (((ProductData.price_discount.old_price*1)+(sumModifyPrice*1))*countProduct);
        Bolean = true;
    }else{
        SumReturn = (((ProductData.price*1)+(sumModifyPrice*1))*countProduct);
        Bolean = false;
    }

    if(returnBoleanDiscount ===true){
        return Bolean;
    }else{
        return SumReturn;
    }

};

