export const loadAdressAffiliate = (CityData) => {
  return async dispatch => {

   // console.log('Запрашиваем адреса самовывоза -------------->>>>>>>>>>>',CityData);
    const DataAdressServer = await ServerDB.getAdressAffiliate(CityData.cityId);

     // console.log("DataAdressServer = ",DataAdressServer,DataAdressServer?.activeAffiliate);

     let activeAffiliate = true;
     let activeDelivered = true;
     let typeDeliverySystem = true;

     if(DataAdressServer?.activeAffiliate === false){
       activeAffiliate = false;
       typeDeliverySystem = true;
     }
     if(DataAdressServer?.activeDelivered === false){
       activeDelivered = false;
       typeDeliverySystem = false;
     }

    dispatch({
      type: SET_ADRESS_AFFILIATE,
      payload: DataAdressServer,
      activeAffiliate:activeAffiliate,
      activeDelivered:activeDelivered,
      textBlockAffiliateDeactive:DataAdressServer?.textBlockAffiliateDeactive || "К сожалению, самовывоз недоступен, вырерите доставку!",
      textBlockDeliveredDeactive:DataAdressServer?.textBlockDeliveredDeactive || "К сожалению, доставка недоступна, вырерите самовывоз!",
      displayAll:true,
      displayAllAffiate:true,
      typeDeliverySystem:typeDeliverySystem
    });
  }
};


export const loadCatalogProduct = (CityData) => {
  return async dispatch => {
    const arrayCategory = await ServerDB.getCatalogProducts(CityData.cityId);
    const arrayDopProduct = await ServerDB.getCatalogDopProducts(CityData.cityId);

    // console.log('Загружаем слайды')
    const newArrayCategory = Object.keys(arrayCategory.list).map((key,data)=>{
      return {
        id:key,
        img:URL_SITE+"/images/API_IMG/"+arrayCategory.list[key].imgCategory,
        title:arrayCategory.list[key].Title,
        orders:arrayCategory.list[key].position,
        FilterCategory:arrayCategory.list[key]?.filterCategory
      };
     });

     //console.log(newArrayCategory,"Категории загружены");
    dispatch({
      type: LOAD_CATALOG,
      payload: {
        Category:newArrayCategory,
        Catalog:arrayCategory.list,
        CatalogDopProduct:arrayDopProduct,
        CatalogRecommend:arrayCategory.listRecommend,
        PreorderCategoryDopsLimit:arrayCategory.listPreorderCategoryDopProduct
      }
    });

    // setTimeout(()=>{
    //   dispatch(loadCatalogProduct(CityData))
    // });
  }
};


