export const loadCurrentVersionApp = (nowVersionApp) => {
  return async dispatch => {
    //console.log(nowVersionApp);
    const Version = await ServerDB.getCurrentVersionApp();
   // console.log('Получили ответ - ',Version);

    if(Version === false){

    }else{
      if(nowVersionApp*1 < Version.currentVersionGoogle*1) {
        dispatch({
          type: SET_UPDATE_APP_STATUS,
          payload: {
            updateAppStatus:true,
            linkUpdateAppiOS:Version.linkUpdateAppiOS,
            linkUpdateAppAndroid:Version.linkUpdateAppAndroid,
            linkUpdateAppAndroidNoGoogle:Version.linkUpdateAppAndroidNoGoogle,
            linkUpdateAppAndroidXiaomi:Version.linkUpdateXiaomi,
          }
        });
      }
    }
    setTimeout(()=>{dispatch(loadCurrentVersionApp(nowVersionApp));},20000);
  };

};


