import React, {useCallback, useEffect, useRef, useState} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { THEME } from '../../theme';
import {
    addProductBasket, addProductBasketOff,
    loadAuthInfo,
    loadCity, setSelectAdresAffiliate,
    setTimeSendSms,
    setTypeDeliverySystem
} from "../../store/actions/post";
import {Platform, Pressable, StyleSheet, Text, TouchableOpacity, View} from "react-native";
import {ServerDB} from "../../serverDb";
import { MaterialCommunityIcons } from '@expo/vector-icons';
import { Entypo } from '@expo/vector-icons';
import {TypeDataReturnRender} from "../TypeDataReturnRender";
import { MaterialIcons } from '@expo/vector-icons';
import {CheckCategoryOff} from "../CheckCategoryOff";
import CheckBasketSelectAdresDelivered from "../SystemFunction/CheckBasketSelectAdresDelivered";


export const SelectionBlock = ({navigation, route})=>{
    const dispatch = useDispatch();

    //const [typeDelivery,setTypeDelivery] = useState(true);
    const basket = useSelector(state => state.post.basket);
    const basketOff = useSelector(state => state.post.basketOff);

    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);

    const DeliveryAdresSystem = useSelector(state => state.post.adressSelect);
    const AffiliateAdresSystem = useSelector(state => state.post.affiliateSelect);
    const AuthUserStatus = useSelector(state => state.post.AuthUserStatus);
    const ActiveAffiliate = useSelector(state => state.post.activeAffiliate);
    const ActiveDelivered = useSelector(state => state.post.activeDelivered);



    let DeliveryAdres = '';
    let PickupAdres = '';
    let ShowHeadBlock = true;

    if(ActiveAffiliate === false || ActiveDelivered === false){
        ShowHeadBlock = false;
    }

    let LoadSystemCatalogTypeDelivery = false;

    switch (ActiveDelivered) {
        case false:
            DeliveryAdres = 'Доставка недоступна!';
            break;
        case true:
        default:
            if(DeliveryAdresSystem === null){
                //setDeliveryAdress('Белгород, ул. Садовая, 67');
                DeliveryAdres = 'Адрес не выбран';
            }else {
                DeliveryAdres = <>
                    <TypeDataReturnRender data={DeliveryAdresSystem.city} type="" separator={false}/>
                    <TypeDataReturnRender data={DeliveryAdresSystem.street} type="ул. " separator=", "/>
                    <TypeDataReturnRender data={DeliveryAdresSystem.home} type="д. " separator=", "/>
                </>
            }
    }

    switch (ActiveAffiliate) {
        case false:
            PickupAdres = 'Самовывоз недоступен!';
            break;
        case true:
        default:
            if(AffiliateAdresSystem === null){
                PickupAdres = 'Точка не выбрана';
                // dispatch(setSelectAdresAffiliate(idAdres));
                // if(TypeDeliverySystem === false ) {
                //     if (!AuthUserStatus) {
                //         setTimeout(() => openAffiliateHandler([{id: 1, title: "Заголовок страницы", booked: false}]), 1000);
                //     }
                // }
            }else{
                //console.log(AffiliateAdresSystem);
                PickupAdres = <>
                    <TypeDataReturnRender data={AffiliateAdresSystem.title} type="" separator={false}/>
                </>;
            }
    }




    const openAdressHandler = AdressList => {
        //console.log('Открываем товар');
        navigation.navigate('AdressList', {
            productId: AdressList.id,
            title: AdressList.title,
            booked: AdressList.booked,
            productData:AdressList
        });
    };

    const openAffiliateHandler = AdressList => {
        //console.log('Открываем товар');
        navigation.navigate('AffiliateList', {
            productId: AdressList.id,
            title: AdressList.title,
            booked: AdressList.booked,
            productData:AdressList
        });
    };


    useEffect(() => {
        //dispatch(loadDeliveryAdres());
    }, [dispatch]);


    const handlsetTypeDelivery = async (e)=>{
       // setTypeDelivery(e);
        //CheckBasket();
        await dispatch(setTypeDeliverySystem(e));
        let NewBasketData = await CheckBasketSelectAdresDelivered(basket,DeliveryAdresSystem,AffiliateAdresSystem,e,basketOff );
        await dispatch(addProductBasket(NewBasketData.OkBasket));
        await dispatch(addProductBasketOff(NewBasketData.OffBasket));
    };

    const DeliveryAdresBlock = ActiveDelivered ?
            <TouchableOpacity
                style={{flexDirection:"row",justifyContent:"center",alignItems:"center",width:"100%"}}
                onPress={
                    ()=>openAdressHandler([{id:1,title:"Заголовок страницы",booked:false}])
                }
            >
                <MaterialCommunityIcons name="truck-fast" size={24} color="black" />
                <Text style={styles.textIconCenter}> {DeliveryAdres}</Text>
                <View style={styles.arrowRight}><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
            </TouchableOpacity>
        :
        <>
            <MaterialCommunityIcons name="truck-fast" size={24} color="black" />
            <Text style={styles.textIconCenter}> {DeliveryAdres}</Text>
        </>

    ;
    const PickupAdresBlock = ActiveAffiliate ?
        <TouchableOpacity
            style={{flexDirection:"row",justifyContent:"center",alignItems:"center",width:"100%"}}
            onPress={
                ()=>openAffiliateHandler([{id:1,title:"Заголовок страницы",booked:false}])
            }
        >
                <Entypo name="shop" size={24} color="black" />
                <Text style={styles.textIconCenter}> {PickupAdres}</Text>
            <View style={styles.arrowRight}><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
        </TouchableOpacity>
        :
        <>
            <Entypo name="shop" size={24} color="black" />
            <Text style={styles.textIconCenter}> {PickupAdres}</Text>
        </>
    ;


    return (
        <View style={styles.centerBlock}>
            {ShowHeadBlock === true ?
                <View style={styles.changeBlock}>
                    <Pressable
                        style={!TypeDeliverySystem ? [styles.Pressable, styles.activePressable] : styles.Pressable}
                        onPress={() => {
                            handlsetTypeDelivery(false)
                        }}
                    >
                        <Text style={styles.textCenter}>Самовывоз</Text>
                    </Pressable>
                    <Pressable
                        style={TypeDeliverySystem ? [styles.Pressable, styles.activePressable] : styles.Pressable}
                        onPress={() => {
                            handlsetTypeDelivery(true)
                        }}
                    >
                        <Text style={styles.textCenter}>Доставка</Text>
                    </Pressable>
                </View>
                : false
            }
            <View style={ShowHeadBlock === true ? styles.AdressStr : styles.AdressStrNoBorder }>
                {TypeDeliverySystem ? DeliveryAdresBlock : PickupAdresBlock}
            </View>
        </View>
    )

};

const styles = StyleSheet.create({
    centerBlock:{
        marginHorizontal:10,
        marginVertical: 20,
        backgroundColor:"#f2f2f2",
        borderRadius:8,
        paddingHorizontal:10,
        paddingVertical:10
    },
    changeBlock:{
        flexDirection:'row',
        justifyContent:'space-between',
        alignItems:'center',
        backgroundColor:"#d6d6d6",
        paddingVertical:2,
        paddingHorizontal:2,
        borderRadius:8
    },
    Pressable:{
        color:'#333333',
        paddingHorizontal: 0,
        paddingVertical: 8,
        width:'50%',
        borderRadius: 8
    },
    activePressable:{
        backgroundColor: "#fff",
        color:'#333'
    },
    textCenter:{
        textAlign: 'center',
        fontFamily:'open-bold, segoe ui, Arial'
    },
    AdressStr:{
        flexDirection:'row',
        justifyContent: 'center',
        alignItems: 'center',
        paddingHorizontal:0,
        paddingTop:10,
        marginTop:13,
        borderColor:"#d6d6d6",
        borderTopWidth:1
    },
    AdressStrNoBorder:{
        flexDirection:'row',
        justifyContent: 'center',
        alignItems: 'center',
        paddingHorizontal:0,
        paddingTop:3,
        paddingBottom:3,
        marginTop:0,
        borderTopWidth:0
    },
    textIconCenter:{
        flexDirection:'row',
        // width:"100%",
        marginLeft:5,
        alignItems:'center',
        justifyContent:'space-between',
        fontSize:12
        // backgroundColor:"#ff7300"
    },
    arrowRight:{
        marginLeft:5,
        paddingTop:Platform.OS === 'ios' ? 0 : 2
    }
});