import {useSelector} from "react-redux";

const CheckBasketSelectAdresDelivered = (basketTMP = [],DeliveryAdresSystem = null,AffiliateAdresSystem = null,TypeDeliverySystem = null,basketOffState = [])=>{
    // console.log('Что за гавно блять');
    if(basketOffState.length > 0){
        basketTMP = [...basketTMP,...basketOffState];
    }
    let NoneCategoryStop = [];


    if(TypeDeliverySystem === true){
        NoneCategoryStop = DeliveryAdresSystem?.OffCategoryData?.OffCategory;
    }else{
        NoneCategoryStop = AffiliateAdresSystem?.JsonNoneCheck?.category_none;
    }

    if(typeof (NoneCategoryStop) === "object"){
        NoneCategoryStop = Object.keys(NoneCategoryStop);
    }

    if(NoneCategoryStop?.length <0 || NoneCategoryStop === undefined || NoneCategoryStop === null){
        NoneCategoryStop = [];
    }

    const getOkBasket = ()=>{
        //Проверяем все ли твоары можно получить на адресе
        let OkBasket = [];
        let OffBasket = [];
        basketTMP.forEach((item, number)=> {

            let AddTrue = true;
            // console.log('какого хуя блять ',item);
            NoneCategoryStop.forEach((idC, nC) => {
                if (item.type[0].categoryId == idC) {
                    AddTrue = false
                }
            });

            if (AddTrue === false) {
                //Если товар не доставляется по адресу или не готовится на точке
                OffBasket.push(item);
            }else{
                OkBasket.push(item);
            }

        });

        return {OkBasket,OffBasket};
    }

    let results = getOkBasket();
    // console.log("Куыгдеы = = = ",results);
    return results;

}

export default CheckBasketSelectAdresDelivered;